import Img from 'next/image';
import styled from 'styled-components';
import { BorderRadius } from '@/styles/theme';

interface Props {
	src: string;
	alt: string;
	width?: number;
	height?: number;
	borderRadius?: BorderRadius;
	objectFit?: 'cover' | 'contain' | 'fill' | 'none' | 'scale-down';
	priority?: boolean;
	placeholder?: 'blur' | 'empty';
	className?: string;
}

const Image = ({
	src,
	alt,
	width = 400,
	height = 300,
	borderRadius = 'medium',
	objectFit = 'cover',
	priority = true,
	className,
}: Props) => {
	return (
		<StyledImage className={`${className}`} $borderRadius={borderRadius}>
			<Img src={src} alt={alt} width={width} height={height} priority={priority} style={{ objectFit }} />
		</StyledImage>
	);
};

interface StyledProps {
	$borderRadius: BorderRadius;
}

const StyledImage = styled.div<StyledProps>`
	width: fit-content;
	height: fit-content;
	overflow: hidden;
	border-radius: ${({ $borderRadius }) => $borderRadius};

	img {
		width: 100%;
		height: 100%;
	}
`;

export default Image;
